.header {
    grid-area: h;
    background-color: #363c42;
}
  
.header img {
    width: 50px;
    padding: 10px 0 0 20px;
}

.loginBlock {
    float: right;
    margin-top: 15px;
}

.loginBlock a {
    color:#ff8066;
}

.loginBtn {
    font-size: 18px;
    line-height: 1.5rem;
    padding: 0.25rem 2rem;
    background: #ff8066;
    color: #fff;
    border-radius: 1.5rem;
    letter-spacing: 0.3px;
    font-weight: normal;
    text-align: center;
    font-weight: 600;
    cursor: pointer;
}

button:hover {
    background: #2186c4;
    border-color: #2186c4;
    color: #fff;
}

.myLogin {
    color: #fff;
}